
/* ==========================================================================
Home Page only (FullScreen Layout)
========================================================================== */

/* -- Basic Layout: http://stackoverflow.com/a/4676510 */
.fullscreen-wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 2;
  color: @home-color-main;

  .home-content {
    position: relative;
    margin-right: @home-nav-width;
    min-height: 100%;
  }

  aside {
    background-color: @home-color-blue-nav;
    min-height: 100%;
    width: @home-nav-width;
    position: absolute;
    top: 0;
    right: 0;
  }
}

/* -- Content */
.home-content {
  main {
    /*center everything: https://css-tricks.com/centering-css-complete-guide/*/
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}


/* -- Fullscreen: https://css-tricks.com/perfect-full-page-background-image/#article-header-id-2 */
.bgimage-container {
  position: fixed;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  z-index: 1;

  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    min-width: 50%;
    min-height: 50%;

  }
}

// --- Farbverlauf
.gradient {
  &:after {
    display: block;
    position: relative;
    background-color:rgba(0, 0, 0, 0.6);
    height: 100%;
    width: 100%;
    content: '';
  }
}
